/*Gray and brand colors*/
/* open-sans-300 - latin_cyrillic */
@font-face {
  font-family: "Open Sans";
  font-style: normal;
  font-weight: 300;
  font-display: swap;
  src: local(""), url("../fonts/open-sans-v18-latin_cyrillic-300.woff2") format("woff2"), url("../fonts/open-sans-v18-latin_cyrillic-300.woff") format("woff"); }

/* open-sans-regular - latin_cyrillic */
@font-face {
  font-family: "Open Sans";
  font-style: normal;
  font-weight: 400;
  font-display: swap;
  src: local(""), url("../fonts/open-sans-v18-latin_cyrillic-regular.woff2") format("woff2"), url("../fonts/open-sans-v18-latin_cyrillic-regular.woff") format("woff"); }

/* open-sans-700 - latin_cyrillic */
@font-face {
  font-family: "Open Sans";
  font-style: normal;
  font-weight: 700;
  font-display: swap;
  src: local(""), url("../fonts/open-sans-v18-latin_cyrillic-700.woff2") format("woff2"), url("../fonts/open-sans-v18-latin_cyrillic-700.woff") format("woff"); }

html {
  box-sizing: border-box; }

*,
*::before,
*::after {
  box-sizing: inherit; }

body {
  position: relative;
  min-width: 320px;
  margin: 0;
  font-family: "Open Sans", "Arial", sans-serif;
  font-size: 16px;
  font-weight: normal;
  line-height: 1.2em;
  color: #212121;
  background-color: #ffffff; }

img {
  max-width: 100%;
  height: auto; }

video {
  width: 100%;
  height: auto; }

h1,
h2,
h3,
h4,
p {
  margin: 0;
  word-wrap: break-word; }

h1 {
  font-size: 48px;
  font-weight: 300; }

a {
  text-decoration: none;
  cursor: pointer; }

fieldset {
  margin: 0;
  padding: 0; }

blockquote {
  margin: 0; }

.visually-hidden:not(:focus):not(:active) {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden; }

.modal {
  display: none; }

.modal__overlay {
  position: fixed;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  z-index: 1000;
  background-color: #2c2c2c; }
  .modal__overlay--show {
    display: block; }

.modal__content {
  position: fixed;
  width: 100%;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  z-index: 1010;
  padding: 2em;
  font-size: 72px;
  font-weight: 300;
  line-height: 1.2em;
  text-align: center;
  color: #e1e1e1; }

.page-header {
  outline: 2px solid darkcyan;
  min-height: 10vh; }

.page-header__title {
  line-height: 1.2em; }

.page-main {
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  gap: 1rem;
  height: 90vh; }

.page-main__inner {
  outline: 1px dotted palevioletred;
  padding: 1em; }

.page-main__title {
  margin-bottom: 1.5em; }

.page-main__text {
  margin-bottom: 0.75em;
  margin-right: 0.5em; }

.page-main__css {
  display: flex;
  align-items: center;
  padding: 1em;
  max-width: 30em;
  min-height: 5em;
  font-size: 20px;
  border: 1px solid #212121;
  border-radius: 5px; }

.preview {
  width: 50%;
  height: 30%;
  margin: 0 auto;
  border: 1px solid #000000;
  border-radius: 5px;
  background: transparent; }

/*родительский блок для позиционирования .range в потоке документа*/
.range {
  outline: 1px dotted red;
  position: relative;
  height: 50px; }

.range__label {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%; }
  .range__label:active ~ .range__wrapper .range__handle {
    background-color: #00afb4; }

/*прозрачный input type="range"*/
/*opacity: 0; этому элементу устанавливает script*/
.range__input {
  position: absolute;
  z-index: 1;
  width: 100%;
  height: 100%;
  outline: none;
  cursor: ew-resize; }

/*обертка для "визуального" range*/
.range__wrapper {
  position: absolute;
  width: 100%;
  height: 10px;
  bottom: 25%;
  left: 0; }

/*обертка для track и progress*/
.range__wrap-inner {
  position: relative;
  width: 100%;
  height: 100%; }

/*track*/
.range__track {
  position: absolute;
  top: 0;
  left: 0;
  display: block;
  width: 100%;
  height: 100%;
  background-color: gray;
  border: 1px solid #000000;
  border-radius: 4px; }

/*progress*/
.range__progress {
  position: absolute;
  top: 0;
  right: 0;
  height: 100%;
  width: 0;
  background-color: #b3b3b3;
  border: 1px solid #000000;
  border-radius: 4px; }

/*handle - смещающийся элемент ползунка*/
.range__handle {
  position: absolute;
  bottom: -70%;
  height: 25px;
  width: 20px;
  border: 1px solid;
  border-radius: 5px;
  background-color: #e1e1e1; }

.range__value {
  position: absolute;
  top: -5%;
  right: 0;
  width: 55px;
  height: 25px;
  font-size: 18px;
  font-weight: 700;
  display: flex;
  justify-content: center;
  align-items: center; }

.range__input-color-prew {
  margin-bottom: 5em; }

.slider {
  margin-bottom: 1em; }
